/* Navigation */

.header-navigation{
  padding: 0;
  display: flex;
  align-items: center;
  /* margin-left: auto; */
  margin-right: 10px;
}

.header-navigation ul{
  padding: 0;
}

.header-navigation li{
  list-style: none;
  position: relative;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500
}

.header-navigation li::before{
  display: none;
}

.header-navigation > li:hover > a {
  --tw-text-opacity: 1;
  color: rgb(1 89 65 / var(--tw-text-opacity));
}

.header-navigation > li:hover > a svg path {
  fill: #015941;
}

.header-navigation li.has-submenu:hover > .submenu {
  visibility: visible;
}

.header-navigation > li{
  padding: 20px;
}

.header-navigation .submenu{
  display: flex;
  position: absolute;
  top: 100%;
  left: -20px;
  visibility: hidden;
  /* background: #343538; */
  border-radius: 10px;
  padding: 40px;
  white-space: nowrap;
  box-shadow: 0px 2px 18px 2px #ccccccb0;
}

.header-navigation .submenu li{
  padding-bottom: 20px;
  font-weight: 300
}

.header-navigation .submenu li a:hover {
  --tw-text-opacity: 1;
  color: rgb(1 89 65 / var(--tw-text-opacity));
}

.header-navigation .submenu .submenu-title{
  font-weight: 600
}

.header-navigation .submenu > ul{
  margin-right: 40px
}

.header-navigation .submenu > ul:last-child{
  margin-right: 0
}

.header-navigation .submenu > ul > li:last-child{
  padding-bottom: 0;
}

.close-navigation{
  display: none;
}

@media (min-width: 1024px) and (max-width: 1279px) {
  .header-navigation li{
    font-size: 12px;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .header-navigation > li{
    padding: 10px;
  }

  .header-navigation li{
    font-size: 12px;
  }

  .header-navigation .submenu{
    left: -30px;
  }
}

@media (max-width: 1023px) {
  .header-navigation-wrap{
    display: none !important;
  }

  .header-navigation-wrap.active{
    display: flex !important;
    /* flex-direction: column-reverse !important; */
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    min-width: 80%;
    background-color: #015941;
    box-shadow: 1px 15px 7px 2px #015941;
    flex-direction: column;
    padding: 20px;
    align-items: start;
    padding-top: 60px;
    padding-bottom: 40px;
    margin: 0;
    justify-content: flex-start !important;
    overflow-y: auto;
  }

  .header-navigation{
    display: flex;
    flex-direction: column;
    margin: 0;
  }

  .close-navigation{
    display: block;
    position: fixed;
    content: '';
    width: 15px;
    height: 15px;
    background-image: url('../public/images/common/exit.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    right: 32px;
    top: 22px;
    cursor: pointer;
  }

  .header-navigation > li{
    padding: 20px 0;
    width: 100%;
  }

  .header-navigation > li a{
    font-size: 20px;
    color: #fff;
  }

  .header-navigation > li a svg{
    width: 25px;
    margin-left: 5px;
  }

  .header-navigation > li a svg path{
    fill: #fff;
  }

  .header-navigation > li.has-submenu{
    background-image: url('../public/images/common/chevron-forward.svg');
    background-repeat: no-repeat;
    background-size: 7px;
    background-position: center right;
  }

  .submenu-level-1 ul li a{
    font-weight: 500;
    background-image: url('../public/images/common/chevron-forward.svg');
    background-repeat: no-repeat;
    background-size: 7px;
    background-position: center right;
  }

  .header-navigation .submenu{
    flex-direction: column;
    visibility: visible;
    position: unset;
    background: unset;
    box-shadow: unset;
    padding: 15px;
  }

  .header-navigation .submenu ul{
    margin-right: 0;
    margin-bottom: 25px;
  }

  .header-navigation .submenu ul > li{
    padding: 10px;
  }

  .header-navigation .submenu ul > li > a{
    font-size: 15px;
  }

  .header-navigation .submenu .submenu-title{
    color: #fff;
  }

  .header-navigation .submenu ul + ul{
    margin-bottom: 0;
  }

  .header-navigation > li:hover > a {
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity));
  }

  .header-navigation > li:hover > a svg path {
    fill: #FFF;
  }

  .header-navigation .submenu li a:hover {
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity));
  }
}

